.navbar{
    background-color: #111;
    color: #fff;
    font-size: 1.5rem;
    font-weight: 600;
    padding: 0.5rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    width: 100%;
    height: 9rem;

}

.navbar__title{
    font-size: 2.5rem;
    font-weight: 700;
    padding-left: 2rem;
    
    color: #fb0909;
    text-decoration: none;
}

#nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 2rem;
    color: #fb0909;
    padding: 0 2rem;
}
#nav:hover{
    color: #fff;
}