





.banner{
    margin-top: 9rem;
    background-image: url(./Images/2.gif);
    background-color: #111;
    height: 560px;
    color: white;
    
}
.banner__title{
    font-size: 3rem;
    font-weight: 700;
    text-align: center;
    padding-top: 2rem;
    
}
.banner__buttons{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    padding-left: 1%;


}
.banner__button{
    /* padding-left: 10%; */
    margin-left: 4rem;
    /* color: white; */
}

 .banner__button:hover{
    background-color: #fff;
    color: #d41818;
    border: 1px solid #fb0909;
} 
.banner__description{
margin-top: 10%;
text-align: center;
}
.banner--fadeBottom{
    height: 2.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37,37,37,0.61), #111);
    /* background-image: linear-gradient(180deg, transparent, rgba(37,37,37,0.61), #111); */
}
